.label-switch {
  border-radius: 22px;
  cursor: pointer;
  display: inline-block;
  height: 22px;
  position: relative;
  width: 42px;
  margin-bottom: 0; }
  .label-switch input[type="checkbox"] {
    display: none; }
    .label-switch input[type="checkbox"] + .checkbox {
      transition: all 0.3s ease;
      background: #e8e8e8;
      border-radius: 22px;
      border: none;
      cursor: pointer;
      height: 22px;
      margin: 0;
      padding: 0;
      position: relative;
      width: 42px;
      z-index: 0; }
      .label-switch input[type="checkbox"] + .checkbox:before {
        position: absolute;
        top: 2px;
        left: 2px;
        transform: scale(1);
        transition: all 0.3s ease;
        background: white;
        border-radius: 22px;
        content: "";
        height: 18px;
        width: 38px;
        z-index: 1; }
      .label-switch input[type="checkbox"] + .checkbox:after {
        position: absolute;
        top: 2px;
        left: 2px;
        height: 18px;
        width: 18px;
        transition: all 0.3s ease;
        background: white;
        border-radius: 18px;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
        content: "";
        z-index: 2; }
    .label-switch input[type="checkbox"]:checked + .checkbox {
      background: #43AC6A; }
      .label-switch input[type="checkbox"]:checked + .checkbox:before {
        transform: scale(0); }
      .label-switch input[type="checkbox"]:checked + .checkbox:after {
        left: 22px; }
  .label-switch.small {
    border-radius: 16px;
    height: 16px;
    width: 30px; }
