      border-radius: 16px;
      height: 16px;
      width: 30px; }
      .label-switch.small input[type="checkbox"] + .checkbox:before {
        border-radius: 16px;
        height: 12px;
        width: 26px; }
      .label-switch.small input[type="checkbox"] + .checkbox:after {
        height: 12px;
        width: 12px;
        border-radius: 12px; }
    .label-switch.small input[type="checkbox"]:checked + .checkbox:after {
      left: 16px; }

