.widget {
    overflow: hidden;
    &-icon {
        display: inline-block;
        width: 48px;
        height: 48px;
        line-height: 48px;
        text-align: center;

        i {
            font-size: 22px;
        }
    }
    &-bg-icon {
        display: block;
        position: absolute;
        right: 15px;
        top: 0;
       
        font-size: 115px;
        color: white;
        opacity: .3;
    }

    .panel-body {
        
    }
}

.widget {
  position: relative;
  & > div {
    position: relative;
    z-index: 1;
  }
  &:after {
    background-size: cover;
    content: " ";
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    position: absolute;
    z-index: 0;
    opacity: .69;
  }
}
.widget-1 {
  &:after {
    background-image: url("#{$assets-url}/img/dashboard/pages_hero.jpg");
    background-size: cover;
    content: " ";
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    position: absolute;
    z-index: 0;
    opacity: .69;
  }
  &:before {
    background-image: url('#{$base-img-url}/linear_gradient.png');
    background-repeat: repeat-x;
    content: " ";
    left: 0;
    right: 0;
    height: 325px;
    bottom: 0;
    position: absolute;
    z-index: 1;
  }
  & > .panel-body {
    // padding: 54px 37px 37px 37px;
    .company:nth-child(2) > div {
      display: table;
      margin: 0 auto;
      width: 127px;
    }
  }
  & > .panel-body > * {
    z-index: 1;
  }
  & > .panel-body > *:not(.pull-bottom) {
    position: relative;
  }
  & > .panel-body .pull-bottom {
    padding: 0 49px 56px 42px;
  }
  span.label {
    color: #fff;
    background: rgba(255, 255, 255, .3);
  }
}
.widget-2 {
  &:after {
    background-image: url("#{$assets-url}/img/social/person-cropped.jpg");
  }
}
